// logo
.logo_image{
    height: 43px;
    width: 156px;
    
}
// header-container
.page-header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
}


// авторизация
.signup__btn {
    border: none;
    background-color: $white-bg-color;
}
// слайдер
.header-switcher {
    align-items: center;
    gap: 10px;
    display: flex;

    &__item-light {
            color: $accent-color;
            font-size: 18px;
            line-height: 24px;
        }
    &__item-dark {
    font-size: 18px;
    line-height: 24px;
    color: $primary-dark-text-color;
    }

    &__switch {
        width: 40px;
        height: 20px;
        display: inline-block;
        position: relative;
    }

    &__input {
        opacity: 0;
        width: 0;
        height: 0;
    }
}


.header-switcher__input:checked+.header-switcher__slider{
    background-color: #fd5103;
    border: 1px solid #fcfcfc;
    border-radius: 20px;
 }

.header-switcher__input:checked+.header-switcher__slider:before {
    background-color: #fcfcfc;
        transform: translate(20px);
}
.header-switcher__input:checked .header-switcher__item-dark {
    color: #fcfcfc;
}

.header-switcher__slider.round {
    width: 40px;
    height: 20px;
    background-color: $white-bg-color;
    border: 1px solid $accent-color;
    border-radius: 20px;
    position: absolute;
    cursor: pointer;
}

.header-switcher__slider.round::before {
    width: 12px;
    height: 12px;
    background-color: $accent-color;
    border-radius: 50%;
}

.header-switcher__slider{
    &::before {
            content: "";
            height: 26px;
            width: 26px;
            background-color: $white-bg-color;
            transition: all .4s;
            position: absolute;
            bottom: 3px;
            left: 2px;
        }}




// форма

.form-submit {
    width: 100%;
    max-width: 288px;
    height: 40px;
    border: 1px solid $accent-color ;
    border-radius: 4px;
    transition-property: outline;
    transition-duration: $animation;
    padding: 8px;
    display: flex;
    align-items: center;
    margin-right: 40px;


    &__input {
            max-width: 247px;
            border: none;
            background-color: $white-bg-color;
            
    transition-duration: $animation;
        }
    
    &__button {
        padding: 0%;
        border: none;
        background-color: $white-bg-color;
        padding-right: 8px;
        display: flex;
        align-items: center;
    
    }

    &__input {
        height: 24px;
        width: 100%;
        font-size: 14px;
        color: #5F6775;
        padding-left: 8px;
    }  
}

.form-submit__input:focus {
    outline: 0;
    outline-offset: 0;
}

.line {
    width: 1px;
    height: 40px;
    background-color: $accent-color;
}
input::placeholder {
    font-size: 14px;
    color: #5F6775;
}


.mobile-menu__burger-btn {
    background-color: var(--white-bg-color);
    border: 0;
    padding: 0;
    line-height: 0;
    display: block;
    transition-duration: $animation;
}


// мобильное меню
.mobile-menu{
    opacity: 0;
    visibility:hidden;
    pointer-events: auto;
    z-index: 8;
    padding: 20px 20px;
    padding-top: 22px;
    position: absolute;
    top: 0;
    left: 0;
    background-color: $white-bg-color;
    flex-direction: column;
    align-items: center;
    display: flex;
    width: 100%;
    height: 100%;
    transition: opacity $animation;

    &.is-open {
            opacity: 1;
            visibility: visible;
            pointer-events: auto;
            
            
        }
    &__close-btn {
        z-index: 9;
        width: 47px;
        height: 39px;
        background-color: rgba(0, 0, 0, 0);
        border: none;
        display: block;
        position: absolute;
        top: 24px;
        right: 16px;
        
    }
}

.header_logo-mobile{
    display: block;
    position: absolute;
    left: 20px
}


.site-nav__favorites.mobile {
     visibility: hidden;
}
 
 .site-nav__favorites-mobile.is-open{
    opacity: 1;
        visibility: visible;
        pointer-events: auto;
}
     
 .site-nav__favorites.is-open {
     opacity: 1;
     visibility: visible;
     pointer-events: auto;
     
 }
     
 .site-nav__favorites.mobile{
     z-index: 2;
     min-width: 213px;
     border: 1px solid $accent-color;
     border-radius: 4px;
     bottom: 0;
 }

.site-nav-mobile__item{
padding-bottom:15px;}

.site-nav__item-menu{
padding:15px;}
.site-nav__link-menu{
    color: #000000;
}

.site-nav-mobile{
flex-direction: column;
display: flex;
position: relative;}





