.page-header__container {
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.signup__btn {
  background-color: #fcfcfc;
  border: none;
}

.header-switcher {
  align-items: center;
  gap: 10px;
  display: flex;
}

.header-switcher__item-light {
  color: #fd5103;
  font-size: 18px;
  line-height: 24px;
}

.header-switcher__item-dark {
  color: #5f6775;
  font-size: 18px;
  line-height: 24px;
}

.header-switcher__switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.header-switcher__input {
  opacity: 0;
  width: 0;
  height: 0;
}

.header-switcher__input:checked + .header-switcher__slider {
  background-color: #fd5103;
  border: 1px solid #fcfcfc;
  border-radius: 20px;
}

.header-switcher__input:checked + .header-switcher__slider:before {
  background-color: #fcfcfc;
  transform: translate(20px);
}

.header-switcher__input:checked .header-switcher__item-dark {
  color: #fcfcfc;
}

.header-switcher__slider.round {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background-color: #fcfcfc;
  border: 1px solid #fd5103;
  border-radius: 20px;
  position: absolute;
}

.header-switcher__slider.round:before {
  width: 12px;
  height: 12px;
  background-color: #fd5103;
  border-radius: 50%;
}

.header-switcher__slider:before {
  content: "";
  height: 26px;
  width: 26px;
  background-color: #fcfcfc;
  transition: all .4s;
  position: absolute;
  bottom: 3px;
  left: 2px;
}

.form-submit {
  width: 100%;
  max-width: 288px;
  height: 40px;
  transition-property: outline;
  transition-duration: 350ms cubic-bezier(.4, 0, .2, 1);
  border: 1px solid #fd5103;
  border-radius: 4px;
  align-items: center;
  margin-right: 40px;
  padding: 8px;
  display: flex;
}

.form-submit__input {
  max-width: 247px;
  transition-duration: 350ms cubic-bezier(.4, 0, .2, 1);
  background-color: #fcfcfc;
  border: none;
}

.form-submit__button {
  background-color: #fcfcfc;
  border: none;
  align-items: center;
  padding: 0% 8px 0% 0%;
  display: flex;
}

.form-submit__input {
  height: 24px;
  width: 100%;
  color: #5f6775;
  padding-left: 8px;
  font-size: 14px;
}

.form-submit__input:focus {
  outline-offset: 0;
  outline: 0;
}

.line {
  width: 1px;
  height: 40px;
  background-color: #fd5103;
}

input::-ms-input-placeholder {
  color: #5f6775;
  font-size: 14px;
}

input::placeholder {
  color: #5f6775;
  font-size: 14px;
}

.mobile-menu__burger-btn {
  background-color: var(--white-bg-color);
  transition-duration: 350ms cubic-bezier(.4, 0, .2, 1);
  border: 0;
  padding: 0;
  line-height: 0;
  display: block;
}

.mobile-menu {
  opacity: 0;
  visibility: hidden;
  pointer-events: auto;
  z-index: 8;
  width: 100%;
  height: 100%;
  background-color: #fcfcfc;
  flex-direction: column;
  align-items: center;
  padding: 22px 20px 20px;
  transition: opacity .35s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.mobile-menu__close-btn {
  z-index: 9;
  width: 47px;
  height: 39px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  display: block;
  position: absolute;
  top: 24px;
  right: 16px;
}

.header_logo-mobile {
  display: block;
  position: absolute;
  left: 20px;
}

.site-nav__favorites.mobile {
  visibility: hidden;
}

.site-nav__favorites-mobile.is-open, .site-nav__favorites.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.site-nav__favorites.mobile {
  z-index: 2;
  min-width: 213px;
  border: 1px solid #fd5103;
  border-radius: 4px;
  bottom: 0;
}

.site-nav-mobile__item {
  padding-bottom: 15px;
}

.site-nav__item-menu {
  padding: 15px;
}

.site-nav__link-menu {
  color: #000;
}

.site-nav-mobile {
  flex-direction: column;
  display: flex;
  position: relative;
}

