.keyboard__cstm-summary {
  width: 93px;
  height: 34px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fd5103;
  border-radius: 4px;
  justify-content: space-between;
  align-items: center;
