.fav-ingredients {
  text-align: center;
  padding-top: 40px;
  &__title {
    margin-bottom: 60px;
    color: $secondary-dark-text-color;
    @include font($size: 22px, $line: 42px, $weight: 600);
    @media screen and(min-width:$tablet) {
      font-family: 'Roboto';
      @include font($size: 32px, $line: 42px, $weight: 700);
    }
    @media screen and(min-width:$desktop) {
      font-family: 'Poppins';
      @include font($size: 48px, $line: 72px, $weight: 700);
    }
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 60px;
    @media screen and(min-width:$tablet) {
      gap: 32px;
    }
  }

  &__item {
    padding: 20px;
    border: 1px solid $accent-color;
    border-radius: 4px;
    width: 100%;
    height: 100%;
    text-align: start;
    @media screen and(min-width:$tablet) {
      width: calc((100% - 32px) / 2);
    }
    @media screen and(min-width:$desktop) {
      width: calc((100% - 64px) / 3);
    }
  }

  &__card-title {
    margin-bottom: 16px;
    color: $secondary-dark-text-color;
    font-family: 'Roboto';
    @include font($size: 26px, $line: 42px, $weight: 900);
  }
  @media screen and(min-width:$tablet) {
    margin-bottom: 23px;
    @include font($size: 28px, $line: 33px, $weight: 900);
  }
  @media screen and(min-width:$desktop) {
    margin-bottom: 27px;
    @include font($size: 32px, $line: 38px, $weight: 900);
  }

  &__desc {
    margin-bottom: 20px;
    color: $secondary-dark-text-color;
    text-transform: uppercase;
    @include font($size: 14px, $line: 21px, $weight: 600);
    @media screen and(min-width:$tablet) {
      margin-bottom: 28px;
      @include font($size: 16px, $line: 24px, $weight: 600);
    }
  }

  &__btns {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
  }
  @media screen and(min-width:$tablet) {
    margin-bottom: 20px;
  }
  @media screen and(min-width:$desktop) {
    margin-bottom: 28px;
  }
}
